.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px;
}

a {
  text-decoration: none;
}

input {
  border-radius: 3px;
  margin: 10px;
}

.sticky-custom {
  top: 0;
  position: sticky;
}

.container h1 {
  padding: 5px 10px;
  margin: 10px 0;
  border-radius: 15px;
}

.container h4 {
  margin: 3px 0;
}

.container span {
  font-size: 12px;
}

.container table {
  margin-top: 10px;
}